<template>
    <v-container>
        <v-form ref="form" @submit.prevent="start" id="form-start">
            <v-row justify="center">
                <v-col cols="12" md="6">
                    <v-row dense>
                        
                    </v-row>
                    <v-row dense>
                        <v-col cols="12">
                            <p class="title">Informasi Pribadi</p>
                        </v-col>
                        
                        <v-col v-for="(meta,idx) in person_information" :key="idx" cols="12">
                            <v-select v-if="meta.options" outlined dense :label="meta.label" :rules="rules" :items="meta.options" v-model="meta.value"/>
                            <template v-else-if="meta.ttl">
                            <v-menu
                                ref="menu"
                                v-model="menu"
                                :close-on-content-click="false"
                                :return-value.sync="meta.value"
                                transition="scale-transition"
                                offset-y
                                min-width="auto"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                    v-model="meta.value"
                                    label="Tanggal Lahir"
                                    prepend-icon="mdi-calendar"
                                    readonly
                                    v-bind="attrs"
                                    v-on="on"
                                ></v-text-field>
                                </template>
                                <v-date-picker
                                v-model="meta.value"
                                no-title
                                scrollable
                                >
                                <v-spacer></v-spacer>
                                <v-btn
                                    text
                                    color="primary"
                                    @click="menu = false"
                                >
                                    Cancel
                                </v-btn>
                                <v-btn
                                    text
                                    color="primary"
                                    @click="saveDate($event)"
                                >
                                    OK
                                </v-btn>
                                </v-date-picker>
                            </v-menu>
                            </template>
                            <v-text-field v-else dense outlined v-model="meta.value" :loading="loading" :rules="rules" :label="meta.label" />
                        </v-col>

                        <v-col cols="12">
                            <p class="title">Informasi Akademik</p>
                        </v-col>
                        <v-col v-for="(meta,idx) in academyc_information" :key="idx" cols="12">
                            <div v-if="meta.items">
                                <p class="subtitle-1" v-text="meta.label" />
                                <div v-for="(item,ind) in meta.items" :key="ind" >
                                    <div v-if="item.items">
                                        <div class="d-flex justify-space-between">
                                            <p class="subtitle-2" v-text="item.label" />
                                            <v-btn icon x-small color="primary" @click="addItem(item)">
                                                <v-icon>mdi-plus</v-icon>
                                            </v-btn>
                                        </div>
                                        
                                        <div v-for="(val,index) in item.value" :key="index">
                                            <div class="d-flex justify-space-between">
                                                <p class="body-1">{{item.label}} {{index+1}}</p>
                                                <v-btn icon x-small color="red" @click="item.value.splice(index,1)">
                                                    <v-icon>mdi-minus</v-icon>
                                                </v-btn>
                                            </div>
                                            <v-text-field v-for="(it,indx) in val" :key="indx" dense outlined v-model="it.value" :loading="loading" :label="it.label" />
                                        </div>
                                    
                                    </div>
                                    <!-- <v-textarea v-else-if="item.label == 'Lainnya'" outlined :label="item.label" v-model="item.value"></v-textarea> -->
                                    <v-text-field v-else dense outlined v-model="item.value" :loading="loading" :label="item.label" />
                                </div>
                            </div>
                            <v-text-field v-else dense outlined v-model="meta.value" :loading="loading" :rules="rules" :label="meta.label" />
                        </v-col>

                        <v-col cols="12" class="text-center">
                            <v-btn depressed class="primary" type="submit" form="form-start" :loading="loading" block>Submit</v-btn>
                        </v-col>
                    </v-row>
                </v-col>
            </v-row>
        </v-form>
    </v-container>
</template>

<script>
    import { mapState } from 'vuex'
    
    export default {
        name:'StartDemo',
        data(){
            return{
                person_information:{
                    nama_lengkap:{
                        label:'Nama Lengkap',
                        value:'',
                    },
                    tempat_lahir:{
                        label:'Tempat Lahir',
                        value:'',
                    },
                    tanggal_lahir:{
                        label:'Tanggal Lahir',
                        value:new Date().toISOString().substr(0, 10),
                        ttl:true,
                    },
                    NISN:{
                        label:'NISN',
                        value:'' ,
                    },
                    jenis_kelamin:{
                        label:'Jenis Kelamin',
                        value:'',
                        options:['Laki-Laki','Perempuan']
                    },
                    sma:{
                        label:'Nama SMA',
                        value:'',
                    },
                    smp:{
                        label:'Asal Sekolah SMP',
                        value:'',
                    },
                    jurusan:{
                        label:'Jurusan yang Diinginkan',
                        value:'',
                        options:['IPA','IPS','BAHASA']
                    },
                    cita_cita:{
                        label:'Cita - cita',
                        value:'',
                    },
                },
                academyc_information: {
                    prestasi_akademik:{
                        label:'Prestasi Akademik',
                        items:{
                            rangking_tertinggi:{
                                label:'Rangking Tertinggi Selama SMP',
                                value:'',
                            },
                            // lainnya:{
                            //     label:'Lainnya',
                            //     value:'',
                            // }
                        },
                    },
                    prestasi_non_akademik:{
                        label:'Prestasi Non Akademik',
                        items:{
                            olimpiade: {
                                label:"Olimpiade",
                                value:[],
                                items:true
                            },
                            olahraga: {
                                label:"Olahraga",
                                value:[],
                                items:true
                            },
                            kesenian: {
                                label:"Kesenian",
                                value:[],
                                items:true
                            },
                        },
                    },
                },
                menu:false,
                rules : [
                    v => !!v || 'This field is required'
                ],
            }
        },
        created(){
            // this.userDetail()
            this.person_information.nama_lengkap.value = this.detail.name
        },
        computed:{
            ...mapState({
                loading : state => state.loading,
                user: state => state.userLoggedIn,
                detail : state => state.detail,
                // otherData : state => state.auth.otherData,
            })
        },
        methods:{
            start(){
                if(this.$refs.form.validate()){
                    this.$router.push({name:'sequence-demo'})
                }
            },
            saveDate(){
                // console.log(this.$refs.menu[0])
                this.$refs.menu[0].save(this.person_information.tanggal_lahir.value)
            },
            addItem(item){
                const newItem = new Object({
                    nama:{
                        label:'Nama',
                        value:'',
                    },
                    tahun:{
                        label:'Tahun',
                        value:'',
                    },
                    hasil:{
                        label:'Hasil',
                        value:'',
                    },
                })
                item.value.push(newItem)
            }
        },
    }
</script>

<style lang="scss" scoped>

</style>