<template>
    <v-container fluid class="fill-height">
        <v-row dense align="center" justify="center">
            <v-col sm="12" md="5" class="text-center">
                <v-card outlined class="pa-5">
                    <v-card-text>
                        <v-row>
                        <v-col
                        cols="6"
                        >
                        <v-img src="../assets/logo.png" alt="Logo TMC" eager height="150" contain></v-img>
                        </v-col>

                        <v-col
                        cols="6"
                        >
                            <v-img src="../assets/partner.jpeg" alt="Logo Partner" eager height="150" contain></v-img>
                        </v-col>
                        </v-row>
                        <h2 class="heading my-5">Login</h2>
                        <v-alert v-if="err_msg" type="error" dense>{{err_msg}}</v-alert>
                        <v-alert v-if="!browserCheck" type="warning" dense>
                        Agar lancar dalam melaksanakan tes minat bakat, pastikan anda menggunakan browser Google Chrome dengan versi terbaru.<br>
                        Klik <a href="https://play.app.goo.gl/?link=https://play.google.com/store/apps/details?id=com.android.chrome">Disini</a> untuk mengupdate google chrome.
                        </v-alert>
                        <v-form ref="form" @submit.prevent="handleLogin" id="form-login">
                            <v-text-field dense outlined v-model="username" :loading="loading" :rules="rules" label="Username" />
                            <v-btn depressed class="primary" type="submit" form="form-login" :loading="loading" block>{{login_label}}</v-btn>
                            <div>
                                <br>
                                <b>Kontak Whatsapp:</b><br>
                                087867504803 (Bapak Ferdy)<br>
                                082166601184 (Ibu Thalita)<br>
                            </div>
                            <v-btn text disabled block>copyright &copy; 2020 z-techno</v-btn>
                        </v-form>
                    </v-card-text>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    import {mapActions,mapState} from 'vuex'
    import group_1 from '@/json/group_1.json'
    import group_2 from '@/json/group_2.json'
    import group_3 from '@/json/group_3.json'

    export default {
        name:'Login',
        data(){
            return{
                login_label:'Login',
                username:'',
                err_msg:'',
                browserCheck:false,
                rules: [
                    v => !!v || 'This field is required',
                ],
            }
        },
        created(){

            // var nVer = window.navigator.appVersion;
            var nAgt = window.navigator.userAgent;
            var browserName  = window.navigator.appName;
            var fullVersion  = ''+parseFloat(window.navigator.appVersion); 
            var majorVersion = parseInt(window.navigator.appVersion,10);
            var nameOffset,verOffset,ix;

            // In Opera, the true version is after "Opera" or after "Version"
            if ((verOffset=nAgt.indexOf("Opera"))!=-1) {
             browserName = "Opera";
             fullVersion = nAgt.substring(verOffset+6);
             if ((verOffset=nAgt.indexOf("Version"))!=-1) 
               fullVersion = nAgt.substring(verOffset+8);
            }
            // In MSIE, the true version is after "MSIE" in userAgent
            else if ((verOffset=nAgt.indexOf("MSIE"))!=-1) {
             browserName = "Microsoft Internet Explorer";
             fullVersion = nAgt.substring(verOffset+5);
            }
            // In Chrome, the true version is after "Chrome" 
            else if ((verOffset=nAgt.indexOf("Chrome"))!=-1) {
             browserName = "Chrome";
             fullVersion = nAgt.substring(verOffset+7);
            }
            // In Safari, the true version is after "Safari" or after "Version" 
            else if ((verOffset=nAgt.indexOf("Safari"))!=-1) {
             browserName = "Safari";
             fullVersion = nAgt.substring(verOffset+7);
             if ((verOffset=nAgt.indexOf("Version"))!=-1) 
               fullVersion = nAgt.substring(verOffset+8);
            }
            // In Firefox, the true version is after "Firefox" 
            else if ((verOffset=nAgt.indexOf("Firefox"))!=-1) {
             browserName = "Firefox";
             fullVersion = nAgt.substring(verOffset+8);
            }
            // In most other browsers, "name/version" is at the end of userAgent 
            else if ( (nameOffset=nAgt.lastIndexOf(' ')+1) < 
                      (verOffset=nAgt.lastIndexOf('/')) ) 
            {
             browserName = nAgt.substring(nameOffset,verOffset);
             fullVersion = nAgt.substring(verOffset+1);
             if (browserName.toLowerCase()==browserName.toUpperCase()) {
              browserName = window.navigator.appName;
             }
            }
            // trim the fullVersion string at semicolon/space if present
            if ((ix=fullVersion.indexOf(";"))!=-1)
               fullVersion=fullVersion.substring(0,ix);
            if ((ix=fullVersion.indexOf(" "))!=-1)
               fullVersion=fullVersion.substring(0,ix);

            majorVersion = parseInt(''+fullVersion,10);
            if (isNaN(majorVersion)) {
             fullVersion  = ''+parseFloat(window.navigator.appVersion); 
             majorVersion = parseInt(window.navigator.appVersion,10);
            }

            console.log(''
             +'Browser name  = '+browserName+'<br>'
             +'Full version  = '+fullVersion+'<br>'
             +'Major version = '+majorVersion+'<br>'
             +'navigator.appName = '+navigator.appName+'<br>'
             +'navigator.userAgent = '+navigator.userAgent+'<br>'
            )

            if(!(browserName == "Chrome" && majorVersion >= 79))
            {
                this.browserCheck = false
            }
            else
            {
                this.browserCheck = true
            }
            // this.checkLoggedIn()
        },
        computed:{
            ...mapState({
                loading:state=>state.loading,
                user:state=>state.userLoggedIn
            })
        },
        methods:{
            ...mapActions([
                'login',
                'getDetail',
                'getAnswered',
                'getCategories',
                'getLastCategory'
            ]),
            async handleLogin(){
                this.login_label = 'Silahkan Tunggu...'
                if(this.$refs.form.validate()){
                    this.err_msg = ''
                    var login = await this.login({username:this.username})
                    if(Object.prototype.hasOwnProperty.call(login.data, "user"))
                    {
                        this.$store.dispatch('setUser',login.data.user)
                        window.localStorage.setItem('userLoggedIn',JSON.stringify(login.data.user))
                        window.localStorage.setItem('test_group',login.data.test_group)
                        
                        this.login_label = 'Login Berhasil. Mendownload Detail Pengguna..'
                        var detail = login.data.detail
                        localStorage.setItem('detail',JSON.stringify(detail))
                        this.login_label = 'Detail Terdownload.'
                        if(detail != null && detail.exam != null)
                        {
                            this.login_label += ' Mendownload Soal..'
                            this.$store.dispatch('setDetail',detail)
                            // var getCategories = login.data.categories
                            if(login.data.test_group == 'group_1')
                                localStorage.setItem('categories',JSON.stringify(group_1))

                            if(login.data.test_group == 'group_2')
                                localStorage.setItem('categories',JSON.stringify(group_2))

                            if(login.data.test_group == 'group_3')
                                localStorage.setItem('categories',JSON.stringify(group_3))
    
                            this.login_label = 'Soal Terdownload. Memuat Dashboard...'
                            var getAnswered = login.data.answered
                            localStorage.setItem('answered',JSON.stringify(getAnswered))
    
                            // this.login_label = 'Jawaban Terdownload. Mengecek Sesi Terakhir..'
                            // var getLastCategory = login.data.last_category
                            // localStorage.setItem('lastCategory',JSON.stringify(getLastCategory))
                            // this.login_label = 'Sesi Valid. Memuat Dashboard...'
                        }
                        else
                            this.login_label = 'Memuat Dashboard...'
                        this.$router.push({name:'dashboard'})
                        // location.reload()
                    }
                    // this.checkLoggedIn()
                    // setTimeout(function(){
                    // },2000)
                }
                this.err_msg = 'Login Gagal. Username tidak ditemukan'
                this.login_label = 'Login'
                var vm = this
                setTimeout(function(){
                    vm.err_msg = ''
                },3000)

            },
            // checkLoggedIn(){
            //     if(this.user){
            //         this.$router.push({name:'dashboard'})
            //         location.reload()
            //     }
            // }
        }
    }
</script>

<style lang="scss" scoped>

</style>